.text-top{
  position: fixed;
  top: 50%;
  left:50%;
  transform: translate(-50%,-50%);
  color: #ffc91f;
  justify-content: center;
  align-items: center;
  text-align: center;
  display: flex;
  flex-direction: column;
  gap: 30px;
  z-index: 5;
}
.header-interesse{
  display:flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  font-size: 20pt;
  margin-bottom:35px;
}

#imgInteresses{
  height: auto;
  width: 50%;
  margin-bottom: 25px;
}

#text-bottom{
  margin-bottom: 35px;
 font-size: 20pt;
