.newWindow{
  position: fixed;
  top: 50%;
  left: 50%;
  justify-content: flex-start;
  text-align: left;
  transform: translate(-50%,-50%);
  height: 70%;
  width: 80%;
  background-color: rgb(5, 5, 61);
  border-radius: 25px;
  border: 5px solid #ffc91f;
  z-index: 4;
}

#closePage{
  margin: 25px;
  color: #ffc91f;
  font-size: 15pt;
  width: 25px;
  height: 25px;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  text-align: center;
  border: #ffc91f 3px solid;
  z-index: 4;
}
#closePage:hover{
  color :red;
  border: red 1px solid;
}
