.container-conhecimento{
  position: absolute;
  top: 	50%;
  left: 50%;
  transform: translate(-50%,-50%);
  align-items: center;
  justify-content: center;
  display: flex;
  width:auto;
  flex-direction: column;
  z-index: 5; 

}

.title-conhecimento{
  color: #ffc91f;
  margin-bottom: 30px;
  font-size: 20pt;
  text-align: center;

}
#logo-container{
  justify-content: center;
  align-items: center;
  display: flex;
  gap: 30px;
}
#logoRedesSociais{
  position: absolute;
  height: 50px;
}
:hover#logoRedesSociais{
  transition:  0.3s;
  height: 70px;
}
#containerLogoRedesSociais{
  height: 70px;
  width: 50px;
}


@media (max-width:600px) {
  .container-conhecimento{
    position: absolute;
    top: 	50%;
    left: 50%;
    transform: translate(-50%,-50%);
    align-items: center;
    justify-content: center;
    display: flex;
    width:auto;
    flex-direction: column;
    z-index: 5; 
 
  }
  #logo-container{
    justify-content: center;
    align-items: center;
    display: flex;
    gap: 10px;
  }
  #logoRedesSociais{
    position: absolute;
    height: 30px;
  }
  #containerLogoRedesSociais{
    height: 20px;
    width: 30px;
  }

}