.metas-e-objetivos{
  position: absolute;
  display: flex;
  top: 	50%;
  left: 50%;
  transform: translate(-50%,-50%);
  justify-content: center;
  flex-direction: column;
  align-items: center;
  text-align: center;
  color: #ffc91f;

  z-index: 5;
}
#titulo{
 color: #ffc91f;
 font-size:20pt;
}
#meta-img{
  height: 100px;
}
#meta-img:hover{

  animation: rotate linear infinite 500ms;
}
.grafico{

  display: flex;
  justify-content:center;
align-items: baseline;
  margin-top: 50px;
  color:  rgb(5, 5, 61);
  gap:5em;
  
}
#grafico1{
  position: relative;
  display: flex;
  align-items: flex-end;
  justify-content:center;
  height: 100px;
  width: 70px;
  background-color: #ffc91f;
  font-weight: 800;
  font-size: 15pt;
  border-radius: 25px 25px 0 0;
}
#grafico2{
    display: flex;
  align-items: flex-end;
  justify-content:center;
  height: 150px;
  width: 70px;
  background-color: #ffc91f;
  font-weight: 800;
  font-size: 15pt;
  border-radius: 25px 25px 0 0;
}
#grafico3{
  display: flex;
  align-items: flex-end;
  justify-content:center;
  height: 200px;
  width: 70px;
  background-color: #ffc91f;
  font-weight: 800;
  font-size: 15pt;
  border-radius: 25px 25px 0 0;
}

#container__grafico__objetivo{
  position: relative;
  display: flex;
  flex-direction: column;
  height: auto;
  width: 150px;
  justify-content: center;
  align-items: center;
  gap: 10px;
}
#objetivo1{
  color: #ffc91f;
}
#objetivo2{
  color: #ffc91f;
}
#objetivo3{
  color: #ffc91f;
}


:hover#grafico1{
  background-color: rgb(1, 255, 1);
}

:hover#grafico2{
  background-color: rgb(1, 255, 1);
}

:hover#grafico3{
  background-color: rgb(1, 255, 1);
}

@media(max-width:960px){
  #container__grafico__objetivo{
    width: 60px;
    display: flex;
    flex-direction: column-reverse;
    

  }
  .grafico{

     align-items: last baseline;
     gap: 20px;
     
    }
    #grafico1{
      height: 90px;
      font-size: 10pt;
      width: 50px;
    }
    #grafico2{
      height: 150px;
      font-size: 10pt;
      width: 50px;
    }
    #grafico3{
      height: 190px;
      font-size: 10pt;
    width: 50px;
  }
  #objetivo1{
   font-size: 10pt;
  }
  #objetivo2{
    font-size: 10pt;
    
  }
  #objetivo3{
    font-size: 10pt;

  }
}