.container-proximo-passo{
  position:fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  display: flex;
  flex-direction: column;
  color: #ffc91f;
  justify-content: center;
  align-items: center;
  text-align: center;
  font-size: 20pt;
  z-index: 5;
}


.container-img-logo{
  display: flex;
  flex-direction: row;
  gap: 50px;
}
#imgPassoApasso{
  margin-top: 50px;
  height: 150px;
}