.titulo{
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius:25px ;
  height: 200px;
  width: 500px;
  padding: 10px ;
  background-color: rgba(0, 0, 0, 0.668);
  color:#ffc91f;
  font-size: 15pt;
  font-weight: 500;
  border: #ffc91f 5px solid;
  text-shadow: 1px,1px 3px black;
  text-align: center;
  z-index: 2;
}



@media(max-width:1000px) {
.titulo{
  font-size: 10pt;
  height: 150px;
  width: 290px ;
 text-align: center;

}

}
