.conteudo-menu{
  position: fixed;
  top: 50%;
  left: 50%;
  justify-content:center;
  flex-direction: column;
  gap: 500px;
  text-align: center;
  transform: translate(-50%,-50%);
  display: flex;
  gap: 5px;
  color: #ffc91f;
  justify-content: center;
  align-items: center;
  z-index: 5;
}
#container-img-Projeto{
  position: fixed;
  display: flex;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  margin-left: 350px;
  z-index: 5;
}


#linkProjeto:hover{
font-size: 15pt;
color: rgb(2, 255, 2);
transition: 0.3s;
}
