
.containerImg{
position:absolute;
  height: 150px;
  display: flex;
  width: 150px;
  justify-content:flex-start;
  align-items:center;
  flex-direction: column;
  overflow: hidden;
  margin-bottom: 300px;
  border-radius: 50%;
  border:  5px solid #ffc91f;

  z-index:3;
}
#minhaFoto{
height: 450px;
margin-right: 100px;
margin-top: -25px;
}
.containerImg:hover{
animation: rotate  linear infinite 1000ms;
}
@keyframes rotate {
  to{
    transform: rotateY(0deg);

  }
  from{
    transform: rotateY(360deg);

  }
}

@media (max-width:1000px) {
  .containerImg{
    margin-top: 50px;
    height: 100px;
    width: 100px;
  }
  #minhaFoto{
    height: 290px;
    margin-right: 60px;
    margin-top: -15px;
    }
  
}