.content{
  position: absolute;
  width: 100%;
  height: 70px;
  background-color: rgba(0, 0, 0, 0.401);
  display: flex;
  justify-content: space-around;
  align-items: center;
  color: #FFc91f;
  z-index: 2;
}
#linkStyle:hover{
 color: rgb(2, 255, 2);
  transition: 0.3s;
  
}
#divLinkStyle{
  height: 50px;
  width: 170px;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;

}
#divLinkStyle:hover{

  font-size: 13pt;
  transition: 0.3s;
}


@media(max-width:1000px) {
  .content{
    position: fixed;
    width: 100%;
    height: 70px;
    background-color: rgba(0, 0, 0, 0.401);
    display: flex;
    flex-direction: row;
    gap: 5px;
    text-align: center;
    justify-content: center;
    align-items: center;
    color: #FFc91f;
    z-index: 2;
  }
  #divLinkStyle{
   display: flex;
   justify-content: center;
   align-items: center;
   text-align: center;
    height: 20px;
    width: 60px;
    margin-left: 10px;
  }
  #linkStyle{
    font-size: 8pt;
     
   }
}